// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchSongSheet } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 20,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 接收的歌单
      songSheets: [],
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 查到的歌单数量
      playlistCount: 0,
      // html渲染的歌单名
      vHtmlSongSheetName: '',
      // 是否显示v-html嵌入的歌单名关键字html标签
      isVHtmlSongSheetName: 0
    };
  },

  mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索歌单

    if (sessionStorage.getItem('searchKey')) this.getSearchSongSheet();
  },

  methods: {
    // 搜索歌单(偏移 offset 最多为 300，即最多 16页；这里设置 offset 最多为 280，即最多 15页)
    async getSearchSongSheet() {
      let res = await getSearchSongSheet(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索歌单', res);

      this.playlistCount = res.data.result.playlistCount;
      this.songSheets = res.data.result.playlists;
      if (this.songSheets.length !== 0) this.isLoading = 0; // console.log('this.songSheets', this.songSheets);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新搜索歌单

      this.getSearchSongSheet();
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 歌手关键词高亮显示
    activeSongSheetName(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSongSheetName = 0;
        return string;
      } else {
        this.isVHtmlSongSheetName = 1;
        this.vHtmlSongSheetName = string;
        return '';
      }
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    }

  }
};